// src/services/account.service.ts
import axios from 'axios';

import { Account, CreateAccountDto } from '../types/account.types';
import { API_URL_config } from '../config';
//const API_URL = 'https://localhost:7000/api/Accounts';
const API_URL = `${API_URL_config}/Accounts`;
// Get authorization header
const getAuthHeader = () => {
  const token = localStorage.getItem('token');
  return {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };
};

// Get all accounts
export const getAllAccounts = async (): Promise<Account[]> => {
  const response = await axios.get(API_URL, getAuthHeader());
  return response.data;
};

// Get total balance
export const getTotalBalance = async (): Promise<number> => {
  const response = await axios.get(`${API_URL}/total-balance`, getAuthHeader());
  return response.data;
};

// Create account
export const createAccount = async (data: CreateAccountDto): Promise<Account> => {
  const response = await axios.post(API_URL, data, getAuthHeader());
  return response.data;
};

// Delete account
export const deleteAccount = async (accountId: number): Promise<void> => {
  await axios.delete(`${API_URL}/${accountId}`, getAuthHeader());
};