// src/services/masterBudget.service.ts



import axios from 'axios';
import { MasterBudget, CreateMasterBudgetDto, CreateBudgetDetailDto, BudgetDetail } from '../types/masterBudget.types';
import { API_URL_config } from '../config';
//const API_URL = 'https://localhost:7000/api/MasterBudgets';
const API_URL = `${API_URL_config}/MasterBudgets`;
const getAuthHeader = () => {
  const token = localStorage.getItem('token');
  return {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };
};

export const getAllMasterBudgets = async (): Promise<MasterBudget[]> => {
  const response = await axios.get(API_URL, getAuthHeader());
  return response.data;
};

export const getActiveMasterBudgets = async (): Promise<MasterBudget[]> => {
  const response = await axios.get(`${API_URL}/active`, getAuthHeader());
  return response.data;
};

export const getMasterBudgetById = async (masterBudgetId: number): Promise<MasterBudget> => {
  const response = await axios.get(`${API_URL}/${masterBudgetId}`, getAuthHeader());
  return response.data;
};

export const createMasterBudget = async (data: CreateMasterBudgetDto): Promise<MasterBudget> => {
  const response = await axios.post(API_URL, data, getAuthHeader());
  return response.data;
};

export const deleteMasterBudget = async (masterBudgetId: number): Promise<void> => {
  await axios.delete(`${API_URL}/${masterBudgetId}`, getAuthHeader());
};

export const addBudgetDetail = async (masterBudgetId: number, data: CreateBudgetDetailDto): Promise<BudgetDetail> => {
  const response = await axios.post(`${API_URL}/${masterBudgetId}/details`, data, getAuthHeader());
  return response.data;
};

export const deleteBudgetDetail = async (budgetDetailId: number): Promise<void> => {
  await axios.delete(`${API_URL}/details/${budgetDetailId}`, getAuthHeader());
};