// src/pages/AccountsPage.tsx

import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { getAllAccounts, createAccount, deleteAccount } from '../services/account.service';
import { Account, AccountType } from '../types/account.types';

const AccountsPage: React.FC = () => {
  const navigate = useNavigate();
  const [accounts, setAccounts] = useState<Account[]>([]);
  const [loading, setLoading] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    accountType: 0,
    balance: 0,
    currency: 'PKR'
  });

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
      navigate('/login');
      return;
    }
    fetchAccounts();
  }, [navigate]);

  const fetchAccounts = async () => {
    try {
      setLoading(true);
      const data = await getAllAccounts();
      setAccounts(data);
    } catch (error) {
      console.error('Failed to fetch accounts:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      await createAccount(formData);
      setShowModal(false);
      setFormData({ name: '', accountType: 0, balance: 0, currency: 'PKR' });
      fetchAccounts();
    } catch (error) {
      console.error('Failed to create account:', error);
      alert('Failed to create account');
    }
  };

  const handleDelete = async (accountId: number) => {
    if (!window.confirm('Are you sure you want to delete this account?')) return;
    try {
      await deleteAccount(accountId);
      fetchAccounts();
    } catch (error) {
      console.error('Failed to delete account:', error);
      alert('Failed to delete account');
    }
  };

  const getAccountTypeLabel = (type: number) => {
    switch (type) {
      case 0: return 'Cash';
      case 1: return 'Bank';
      case 2: return 'Credit Card';
      case 3: return 'Wallet';
      case 4: return 'Loan';
      default: return 'Unknown';
    }
  };

  const getAccountIcon = (type: number) => {
    switch (type) {
      case 0: return '';
      case 1: return '';
      case 2: return '';
      case 3: return '';
      case 4: return '';
      default: return '';
    }
  };

  if (loading) {
    return (
      <div className="d-flex justify-content-center align-items-center min-vh-100">
        <div className="spinner-border text-primary" role="status"></div>
      </div>
    );
  }

  return (
    <div className="accounts-container">
      <style>{`
        .accounts-container {
          min-height: 100vh;
          background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
          padding-bottom: 80px;
        }

        .top-header {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
          padding: 1rem;
          border-radius: 0 0 20px 20px;
          box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
        }

        .header-content {
          display: flex;
          justify-content: space-between;
          align-items: center;
          max-width: 1200px;
          margin: 0 auto;
        }

        .page-title {
          font-size: 1.5rem;
          font-weight: 700;
          margin: 0;
        }

        .add-btn {
          background: rgba(255, 255, 255, 0.2);
          border: 1px solid rgba(255, 255, 255, 0.3);
          color: white;
          padding: 0.5rem 1rem;
          border-radius: 15px;
          font-size: 0.9rem;
          transition: all 0.3s ease;
        }

        .add-btn:hover {
          background: rgba(255, 255, 255, 0.3);
        }

        .accounts-list {
          padding: 1rem;
          max-width: 1200px;
          margin: 0 auto;
        }

        .account-card {
          background: white;
          border-radius: 15px;
          padding: 1rem;
          margin-bottom: 0.75rem;
          box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
          display: flex;
          align-items: center;
          gap: 1rem;
          transition: transform 0.2s ease;
        }

        .account-card:active {
          transform: scale(0.98);
        }

        .account-icon {
          width: 50px;
          height: 50px;
          border-radius: 12px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 1.5rem;
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          flex-shrink: 0;
        }

        .account-info {
          flex: 1;
          min-width: 0;
        }

        .account-name {
          font-weight: 600;
          color: #2d3748;
          margin: 0;
          font-size: 1rem;
        }

        .account-type {
          color: #6c757d;
          font-size: 0.85rem;
          margin: 0;
        }

        .account-balance {
          font-size: 1.1rem;
          font-weight: 700;
          color: #667eea;
          margin: 0;
          text-align: right;
        }

        .delete-btn {
          background: #dc3545;
          border: none;
          color: white;
          padding: 0.4rem 0.8rem;
          border-radius: 10px;
          font-size: 0.8rem;
        }

        .modal-overlay {
          position: fixed;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(0, 0, 0, 0.5);
          display: flex;
          align-items: center;
          justify-content: center;
          z-index: 1000;
          padding: 1rem;
        }

        .modal-content {
          background: white;
          border-radius: 20px;
          padding: 1.5rem;
          max-width: 500px;
          width: 100%;
          max-height: 90vh;
          overflow-y: auto;
        }

        .modal-title {
          font-size: 1.3rem;
          font-weight: 700;
          color: #2d3748;
          margin-bottom: 1rem;
        }

        .form-group {
          margin-bottom: 1rem;
        }

        .form-label {
          font-weight: 600;
          color: #2d3748;
          margin-bottom: 0.5rem;
          display: block;
        }

        .form-control, .form-select {
          border-radius: 12px;
          padding: 0.75rem;
          border: 2px solid #e9ecef;
          width: 100%;
        }

        .form-control:focus, .form-select:focus {
          border-color: #667eea;
          box-shadow: 0 0 0 0.2rem rgba(102, 126, 234, 0.15);
        }

        .btn-submit {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          border: none;
          color: white;
          padding: 0.75rem;
          border-radius: 12px;
          width: 100%;
          font-weight: 600;
          margin-top: 0.5rem;
        }

        .btn-cancel {
          background: #6c757d;
          border: none;
          color: white;
          padding: 0.75rem;
          border-radius: 12px;
          width: 100%;
          font-weight: 600;
          margin-top: 0.5rem;
        }

        .empty-state {
          text-align: center;
          padding: 3rem 1rem;
        }

        .empty-icon {
          font-size: 4rem;
          margin-bottom: 1rem;
        }

        .empty-text {
          color: #6c757d;
          font-size: 1.1rem;
        }

        .bottom-nav {
          position: fixed;
          bottom: 0;
          left: 0;
          right: 0;
          background: white;
          border-top: 1px solid #e9ecef;
          padding: 0.5rem 0;
          box-shadow: 0 -4px 15px rgba(0, 0, 0, 0.05);
          z-index: 1000;
        }

        .nav-items {
          display: flex;
          justify-content: space-around;
          align-items: center;
          max-width: 600px;
          margin: 0 auto;
          padding: 0 0.5rem;
        }

        .nav-item {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 0.2rem;
          padding: 0.4rem 0.75rem;
          border-radius: 10px;
          transition: all 0.3s ease;
          cursor: pointer;
          text-decoration: none;
          color: #6c757d;
          flex: 1;
          max-width: 80px;
        }

        .nav-item.active {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
        }

        .nav-icon {
          font-size: 1.3rem;
        }

        .nav-label {
          font-size: 0.7rem;
          font-weight: 600;
        }
      `}</style>

      {/* Header */}
      <div className="top-header">
        <div className="header-content">
          <h1 className="page-title"> Accounts</h1>
          <button className="add-btn btn" onClick={() => setShowModal(true)}>
             Add Account
          </button>
        </div>
      </div>

      {/* Accounts List */}
      <div className="accounts-list">
        {accounts.length === 0 ? (
          <div className="empty-state">
            <div className="empty-icon"></div>
            <p className="empty-text">No accounts yet. Create your first account!</p>
          </div>
        ) : (
          accounts.map((account) => (
            <div key={account.accountId} className="account-card">
              <div className="account-icon">{getAccountIcon(account.accountType)}</div>
              <div className="account-info">
                <h3 className="account-name">{account.name}</h3>
                <p className="account-type">{getAccountTypeLabel(account.accountType)}</p>
              </div>
              <div>
                <p className="account-balance">
                  {account.currency} {account.balance.toLocaleString()}
                </p>
              </div>
              <button 
                className="delete-btn btn"
                onClick={() => handleDelete(account.accountId)}
              >
                
              </button>
            </div>
          ))
        )}
      </div>

      {/* Add Account Modal */}
      {showModal && (
        <div className="modal-overlay" onClick={() => setShowModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <h2 className="modal-title">Add New Account</h2>
            <form onSubmit={handleSubmit}>
              <div className="form-group">
                <label className="form-label">Account Name</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="e.g., Main Wallet"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  required
                />
              </div>

              <div className="form-group">
                <label className="form-label">Account Type</label>
                <select
                  className="form-select"
                  value={formData.accountType}
                  onChange={(e) => setFormData({ ...formData, accountType: Number(e.target.value) })}
                >
                  <option value={0}> Cash</option>
                  <option value={1}> Bank</option>
                  <option value={2}> Credit Card</option>
                  <option value={3}> Wallet</option>
                  <option value={4}> Loan</option>
                </select>
              </div>

              <div className="form-group">
                <label className="form-label">Initial Balance</label>
                <input
                  type="number"
                  className="form-control"
                  placeholder="0"
                  value={formData.balance}
                  onChange={(e) => setFormData({ ...formData, balance: Number(e.target.value) })}
                  required
                />
              </div>

              <div className="form-group">
                <label className="form-label">Currency</label>
                <select
                  className="form-select"
                  value={formData.currency}
                  onChange={(e) => setFormData({ ...formData, currency: e.target.value })}
                >
                  <option value="PKR">PKR - Pakistani Rupee</option>
                  <option value="USD">USD - US Dollar</option>
                  <option value="EUR">EUR - Euro</option>
                  <option value="GBP">GBP - British Pound</option>
                </select>
              </div>

              <button type="submit" className="btn-submit">Create Account</button>
              <button type="button" className="btn-cancel" onClick={() => setShowModal(false)}>
                Cancel
              </button>
            </form>
          </div>
        </div>
      )}

      {/* Bottom Navigation */}
      <div className="bottom-nav">
        <div className="nav-items">
          <div className="nav-item" onClick={() => navigate('/dashboard')}>
            <div className="nav-icon"></div>
            <div className="nav-label">Home</div>
          </div>
          <div className="nav-item" onClick={() => navigate('/transactions')}>
            <div className="nav-icon"></div>
            <div className="nav-label">Transactions</div>
          </div>
          <div className="nav-item active">
            <div className="nav-icon"></div>
            <div className="nav-label">Accounts</div>
          </div>
          <div className="nav-item">
            <div className="nav-icon"></div>
            <div className="nav-label">More</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AccountsPage;