// src/services/category.service.ts


import axios from 'axios';
import { Category, CreateCategoryDto } from '../types/category.types';
import { API_URL_config } from '../config';
//const API_URL = 'https://localhost:7000/api/Categories';

const API_URL = `${API_URL_config}/Categories`;
const getAuthHeader = () => {
  const token = localStorage.getItem('token');
  return {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };
};

export const getAllCategories = async (): Promise<Category[]> => {
  const response = await axios.get(API_URL, getAuthHeader());
  return response.data;
};

export const createCategory = async (data: CreateCategoryDto): Promise<Category> => {
  const response = await axios.post(API_URL, data, getAuthHeader());
  return response.data;
};

export const updateCategory = async (categoryId: number, data: CreateCategoryDto): Promise<Category> => {
  const response = await axios.put(API_URL, { categoryId, ...data }, getAuthHeader());
  return response.data;
};

export const deleteCategory = async (categoryId: number): Promise<void> => {
  await axios.delete(`${API_URL}/${categoryId}`, getAuthHeader());
};

export const seedCategories = async (): Promise<void> => {
  await axios.post(`${API_URL}/seed`, {}, getAuthHeader());
};