// src/pages/MasterBudgetsPage.tsx

import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { getAllMasterBudgets, createMasterBudget, deleteMasterBudget, addBudgetDetail, deleteBudgetDetail } from '../services/masterBudget.service';
import { getAllCategories } from '../services/category.service';
import { MasterBudget, CreateMasterBudgetDto, CreateBudgetDetailDto } from '../types/masterBudget.types';
import { Category } from '../types/category.types';

const MasterBudgetsPage: React.FC = () => {
  const navigate = useNavigate();
  const [budgets, setBudgets] = useState<MasterBudget[]>([]);
  const [categories, setCategories] = useState<Category[]>([]);
  const [parentCategories, setParentCategories] = useState<Category[]>([]);
  const [childCategories, setChildCategories] = useState<Category[]>([]);
  const [loading, setLoading] = useState(true);
  const [showMasterModal, setShowMasterModal] = useState(false);
  const [showDetailModal, setShowDetailModal] = useState(false);
  const [selectedBudgetId, setSelectedBudgetId] = useState<number>(0);
  const [expandedBudget, setExpandedBudget] = useState<number | null>(null);

  const [masterForm, setMasterForm] = useState<CreateMasterBudgetDto>({
    budgetName: '',
    budgetPeriod: 'Monthly',
    startDate: new Date().toISOString().split('T')[0],
    endDate: new Date(new Date().setMonth(new Date().getMonth() + 1)).toISOString().split('T')[0],
    alertThreshold50: true,
    alertThreshold80: true,
    alertThreshold100: true
  });

  const [detailForm, setDetailForm] = useState({
    parentCategoryId: 0,
    categoryId: 0,
    itemName: '',
    allocatedAmount: 0
  });

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
      navigate('/login');
      return;
    }
    fetchData();
  }, [navigate]);

  useEffect(() => {
    // Separate parents and children
    const parents = categories.filter(c => !c.parentCategoryId && c.categoryType === 'Expense');
    const children = categories.filter(c => c.parentCategoryId);
    setParentCategories(parents);
    setChildCategories(children);
  }, [categories]);

  const fetchData = async () => {
    try {
      setLoading(true);
      const [budgetsData, categoriesData] = await Promise.all([
        getAllMasterBudgets(),
        getAllCategories()
      ]);
      setBudgets(budgetsData);
      setCategories(categoriesData);
    } catch (error) {
      console.error('Failed to fetch data:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleCreateMaster = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      await createMasterBudget(masterForm);
      setShowMasterModal(false);
      setMasterForm({
        budgetName: '',
        budgetPeriod: 'Monthly',
        startDate: new Date().toISOString().split('T')[0],
        endDate: new Date(new Date().setMonth(new Date().getMonth() + 1)).toISOString().split('T')[0],
        alertThreshold50: true,
        alertThreshold80: true,
        alertThreshold100: true
      });
      fetchData();
    } catch (error) {
      alert('Failed to create master budget');
    }
  };

  const handleAddDetail = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const dto: CreateBudgetDetailDto = {
        categoryId: detailForm.categoryId,
        itemName: detailForm.itemName,
        allocatedAmount: detailForm.allocatedAmount
      };
      await addBudgetDetail(selectedBudgetId, dto);
      setShowDetailModal(false);
      setDetailForm({ parentCategoryId: 0, categoryId: 0, itemName: '', allocatedAmount: 0 });
      fetchData();
    } catch (error) {
      alert('Failed to add budget detail');
    }
  };

  const handleDeleteMaster = async (masterBudgetId: number) => {
    if (!window.confirm('Delete this master budget and all its details?')) return;
    try {
      await deleteMasterBudget(masterBudgetId);
      fetchData();
    } catch (error) {
      alert('Failed to delete master budget');
    }
  };

  const handleDeleteDetail = async (budgetDetailId: number) => {
    if (!window.confirm('Delete this budget item?')) return;
    try {
      await deleteBudgetDetail(budgetDetailId);
      fetchData();
    } catch (error) {
      alert('Failed to delete budget detail');
    }
  };

  const getProgressColor = (percentage: number) => {
    if (percentage >= 100) return '#ef4444';
    if (percentage >= 80) return '#f59e0b';
    if (percentage >= 50) return '#eab308';
    return '#10b981';
  };

  // Get available child categories based on selected parent
  const availableChildCategories = detailForm.parentCategoryId 
    ? childCategories.filter(c => c.parentCategoryId === detailForm.parentCategoryId)
    : [];

  if (loading) {
    return (
      <div className="d-flex justify-content-center align-items-center min-vh-100">
        <div className="spinner-border text-primary"></div>
      </div>
    );
  }

  return (
    <div className="master-budgets-container">
      <style>{`
        .master-budgets-container {
          min-height: 100vh;
          background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
          padding-bottom: 80px;
        }

        .top-header {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
          padding: 1rem;
          border-radius: 0 0 20px 20px;
          box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
        }

        .header-content {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }

        .page-title {
          font-size: 1.5rem;
          font-weight: 700;
          margin: 0;
        }

        .add-btn {
          background: rgba(255, 255, 255, 0.2);
          border: 1px solid rgba(255, 255, 255, 0.3);
          color: white;
          padding: 0.5rem 1rem;
          border-radius: 15px;
          font-size: 0.9rem;
        }

        .budgets-list {
          padding: 1rem;
        }

        .master-budget-card {
          background: white;
          border-radius: 15px;
          padding: 1.25rem;
          margin-bottom: 1rem;
          box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
        }

        .master-header {
          display: flex;
          justify-content: space-between;
          align-items: flex-start;
          margin-bottom: 1rem;
        }

        .master-title {
          font-size: 1.2rem;
          font-weight: 700;
          color: #2d3748;
          margin: 0;
        }

        .master-period {
          font-size: 0.85rem;
          color: #6c757d;
          margin-top: 0.25rem;
        }

        .master-actions {
          display: flex;
          gap: 0.5rem;
        }

        .btn-add-detail {
          background: #10b981;
          color: white;
          border: none;
          padding: 0.4rem 0.8rem;
          border-radius: 10px;
          font-size: 0.85rem;
        }

        .btn-delete {
          background: #dc3545;
          color: white;
          border: none;
          padding: 0.4rem 0.8rem;
          border-radius: 10px;
          font-size: 0.85rem;
        }

        .master-summary {
          background: #f8f9fa;
          border-radius: 12px;
          padding: 1rem;
          margin-bottom: 1rem;
        }

        .summary-row {
          display: flex;
          justify-content: space-between;
          margin-bottom: 0.5rem;
          font-size: 0.95rem;
        }

        .summary-label {
          color: #6c757d;
        }

        .summary-value {
          font-weight: 600;
          color: #2d3748;
        }

        .progress-bar-container {
          height: 12px;
          background: #e9ecef;
          border-radius: 10px;
          overflow: hidden;
          margin-bottom: 0.5rem;
        }

        .progress-bar {
          height: 100%;
          border-radius: 10px;
          transition: width 0.3s ease, background 0.3s ease;
        }

        .expand-btn {
          background: #667eea;
          color: white;
          border: none;
          padding: 0.5rem;
          border-radius: 10px;
          width: 100%;
          font-weight: 600;
          font-size: 0.9rem;
          margin-top: 0.5rem;
        }

        .details-section {
          margin-top: 1rem;
          padding-top: 1rem;
          border-top: 2px dashed #e9ecef;
        }

        .details-title {
          font-size: 1rem;
          font-weight: 600;
          color: #2d3748;
          margin-bottom: 0.75rem;
        }

        .detail-item {
          background: #f8f9fa;
          border-radius: 12px;
          padding: 1rem;
          margin-bottom: 0.75rem;
          border-left: 4px solid #667eea;
        }

        .detail-header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 0.5rem;
        }

        .detail-name {
          font-weight: 600;
          color: #2d3748;
          font-size: 0.95rem;
        }

        .detail-category {
          font-size: 0.8rem;
          color: #6c757d;
        }

        .detail-delete {
          background: #dc3545;
          color: white;
          border: none;
          padding: 0.3rem 0.6rem;
          border-radius: 8px;
          font-size: 0.75rem;
        }

        .detail-amounts {
          display: flex;
          justify-content: space-between;
          font-size: 0.85rem;
          margin-bottom: 0.5rem;
        }

        .detail-progress {
          height: 8px;
          background: #e9ecef;
          border-radius: 6px;
          overflow: hidden;
          margin-bottom: 0.25rem;
        }

        .detail-progress-bar {
          height: 100%;
          border-radius: 6px;
        }

        .detail-percentage {
          font-size: 0.75rem;
          text-align: right;
          font-weight: 600;
        }

        .empty-state {
          text-align: center;
          padding: 3rem 1rem;
        }

        .empty-icon {
          font-size: 4rem;
          margin-bottom: 1rem;
        }

        .modal-overlay {
          position: fixed;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(0, 0, 0, 0.5);
          display: flex;
          align-items: center;
          justify-content: center;
          z-index: 1000;
          padding: 1rem;
        }

        .modal-content {
          background: white;
          border-radius: 20px;
          padding: 1.5rem;
          max-width: 500px;
          width: 100%;
          max-height: 90vh;
          overflow-y: auto;
        }

        .modal-title {
          font-size: 1.3rem;
          font-weight: 700;
          margin-bottom: 1rem;
        }

        .form-group {
          margin-bottom: 1rem;
        }

        .form-label {
          font-weight: 600;
          margin-bottom: 0.5rem;
          display: block;
          font-size: 0.9rem;
        }

        .form-control, .form-select {
          border-radius: 12px;
          padding: 0.75rem;
          border: 2px solid #e9ecef;
          width: 100%;
        }

        .form-check {
          display: flex;
          align-items: center;
          gap: 0.5rem;
          margin-bottom: 0.5rem;
        }

        .btn-submit {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          border: none;
          color: white;
          padding: 0.75rem;
          border-radius: 12px;
          width: 100%;
          font-weight: 600;
          margin-top: 0.5rem;
        }

        .btn-cancel {
          background: #6c757d;
          border: none;
          color: white;
          padding: 0.75rem;
          border-radius: 12px;
          width: 100%;
          font-weight: 600;
          margin-top: 0.5rem;
        }

        .bottom-nav {
          position: fixed;
          bottom: 0;
          left: 0;
          right: 0;
          background: white;
          border-top: 1px solid #e9ecef;
          padding: 0.5rem 0;
          box-shadow: 0 -4px 15px rgba(0, 0, 0, 0.05);
          z-index: 1000;
        }

        .nav-items {
          display: flex;
          justify-content: space-around;
          max-width: 600px;
          margin: 0 auto;
        }

        .nav-item {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 0.2rem;
          padding: 0.4rem 0.75rem;
          border-radius: 10px;
          cursor: pointer;
          color: #6c757d;
          flex: 1;
          max-width: 80px;
        }

        .nav-item.active {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
        }

        .nav-icon {
          font-size: 1.3rem;
        }

        .nav-label {
          font-size: 0.7rem;
          font-weight: 600;
        }
      `}</style>

      <div className="top-header">
        <div className="header-content">
          <h1 className="page-title"> Master Budgets</h1>
          <button className="add-btn btn" onClick={() => setShowMasterModal(true)}>
             Create
          </button>
        </div>
      </div>

      <div className="budgets-list">
        {budgets.length === 0 ? (
          <div className="empty-state">
            <div className="empty-icon"></div>
            <p>No master budgets yet. Create your first one!</p>
          </div>
        ) : (
          budgets.map((budget) => {
            const percentage = budget.percentageUsed || 0;
            const isExpanded = expandedBudget === budget.masterBudgetId;

            return (
              <div key={budget.masterBudgetId} className="master-budget-card">
                <div className="master-header">
                  <div>
                    <h3 className="master-title">{budget.budgetName}</h3>
                    <p className="master-period">
                      {budget.budgetPeriod}  {new Date(budget.startDate).toLocaleDateString()} - {new Date(budget.endDate).toLocaleDateString()}
                    </p>
                  </div>
                  <div className="master-actions">
                    <button 
                      className="btn-add-detail"
                      onClick={() => {
                        setSelectedBudgetId(budget.masterBudgetId);
                        setShowDetailModal(true);
                      }}
                    >
                      
                    </button>
                    <button 
                      className="btn-delete"
                      onClick={() => handleDeleteMaster(budget.masterBudgetId)}
                    >
                      
                    </button>
                  </div>
                </div>

                <div className="master-summary">
                  <div className="summary-row">
                    <span className="summary-label">Total Budget:</span>
                    <span className="summary-value">PKR {budget.totalBudget.toLocaleString()}</span>
                  </div>
                  <div className="summary-row">
                    <span className="summary-label">Total Spent:</span>
                    <span className="summary-value" style={{ color: getProgressColor(percentage) }}>
                      PKR {budget.totalSpending.toLocaleString()}
                    </span>
                  </div>
                  <div className="summary-row">
                    <span className="summary-label">Remaining:</span>
                    <span className="summary-value" style={{ color: '#10b981' }}>
                      PKR {budget.remainingAmount.toLocaleString()}
                    </span>
                  </div>

                  <div className="progress-bar-container">
                    <div 
                      className="progress-bar"
                      style={{ 
                        width: `${Math.min(percentage, 100)}%`,
                        background: getProgressColor(percentage)
                      }}
                    />
                  </div>
                  <div style={{ textAlign: 'right', fontSize: '0.85rem', fontWeight: 600, color: getProgressColor(percentage) }}>
                    {percentage.toFixed(1)}% Used
                  </div>
                </div>

                <button 
                  className="expand-btn"
                  onClick={() => setExpandedBudget(isExpanded ? null : budget.masterBudgetId)}
                >
                  {isExpanded ? ' Hide Details' : ` Show Details (${budget.details.length})`}
                </button>

                {isExpanded && budget.details.length > 0 && (
                  <div className="details-section">
                    <h4 className="details-title">Budget Items:</h4>
                    {budget.details.map((detail) => (
                      <div key={detail.budgetDetailId} className="detail-item">
                        <div className="detail-header">
                          <div>
                            <div className="detail-name">{detail.itemName}</div>
                            <div className="detail-category">{detail.categoryName}</div>
                          </div>
                          <button 
                            className="detail-delete"
                            onClick={() => handleDeleteDetail(detail.budgetDetailId)}
                          >
                            
                          </button>
                        </div>

                        <div className="detail-amounts">
                          <span>Spent: PKR {detail.currentSpending.toLocaleString()}</span>
                          <span>Budget: PKR {detail.allocatedAmount.toLocaleString()}</span>
                        </div>

                        <div className="detail-progress">
                          <div 
                            className="detail-progress-bar"
                            style={{ 
                              width: `${Math.min(detail.percentageUsed, 100)}%`,
                              background: getProgressColor(detail.percentageUsed)
                            }}
                          />
                        </div>
                        <div className="detail-percentage" style={{ color: getProgressColor(detail.percentageUsed) }}>
                          {detail.percentageUsed.toFixed(1)}%
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            );
          })
        )}
      </div>

      {/* Create Master Budget Modal */}
      {showMasterModal && (
        <div className="modal-overlay" onClick={() => setShowMasterModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <h2 className="modal-title">Create Master Budget</h2>
            <form onSubmit={handleCreateMaster}>
              <div className="form-group">
                <label className="form-label">Budget Name</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="e.g., January 2025 Budget"
                  value={masterForm.budgetName}
                  onChange={(e) => setMasterForm({ ...masterForm, budgetName: e.target.value })}
                  required
                />
              </div>

              <div className="form-group">
                <label className="form-label">Period</label>
                <select
                  className="form-select"
                  value={masterForm.budgetPeriod}
                  onChange={(e) => setMasterForm({ ...masterForm, budgetPeriod: e.target.value })}
                >
                  <option value="Monthly">Monthly</option>
                  <option value="Quarterly">Quarterly</option>
                  <option value="Yearly">Yearly</option>
                </select>
              </div>

              <div className="form-group">
                <label className="form-label">Start Date</label>
                <input
                  type="date"
                  className="form-control"
                  value={masterForm.startDate}
                  onChange={(e) => setMasterForm({ ...masterForm, startDate: e.target.value })}
                  required
                />
              </div>

              <div className="form-group">
                <label className="form-label">End Date</label>
                <input
                  type="date"
                  className="form-control"
                  value={masterForm.endDate}
                  onChange={(e) => setMasterForm({ ...masterForm, endDate: e.target.value })}
                  required
                />
              </div>

              <div className="form-group">
                <label className="form-label">Alert Thresholds</label>
                <div className="form-check">
                  <input
                    type="checkbox"
                    checked={masterForm.alertThreshold50}
                    onChange={(e) => setMasterForm({ ...masterForm, alertThreshold50: e.target.checked })}
                  />
                  <label>50% Alert</label>
                </div>
                <div className="form-check">
                  <input
                    type="checkbox"
                    checked={masterForm.alertThreshold80}
                    onChange={(e) => setMasterForm({ ...masterForm, alertThreshold80: e.target.checked })}
                  />
                  <label>80% Alert</label>
                </div>
                <div className="form-check">
                  <input
                    type="checkbox"
                    checked={masterForm.alertThreshold100}
                    onChange={(e) => setMasterForm({ ...masterForm, alertThreshold100: e.target.checked })}
                  />
                  <label>100% Alert</label>
                </div>
              </div>

              <button type="submit" className="btn-submit">Create Budget</button>
              <button type="button" className="btn-cancel" onClick={() => setShowMasterModal(false)}>
                Cancel
              </button>
            </form>
          </div>
        </div>
      )}

      {/* Add Detail Modal */}
      {showDetailModal && (
        <div className="modal-overlay" onClick={() => setShowDetailModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <h2 className="modal-title">Add Budget Item</h2>
            <form onSubmit={handleAddDetail}>
              <div className="form-group">
                <label className="form-label">Parent Category</label>
                <select
                  className="form-select"
                  value={detailForm.parentCategoryId}
                  onChange={(e) => {
                    setDetailForm({ 
                      ...detailForm, 
                      parentCategoryId: Number(e.target.value),
                      categoryId: 0,
                      itemName: ''
                    });
                  }}
                  required
                >
                  <option value={0}>Select parent category</option>
                  {parentCategories.map(parent => (
                    <option key={parent.categoryId} value={parent.categoryId}>
                      {parent.icon} {parent.name}
                    </option>
                  ))}
                </select>
              </div>

              {detailForm.parentCategoryId > 0 && (
                <div className="form-group">
                  <label className="form-label">Category</label>
                  <select
                    className="form-select"
                    value={detailForm.categoryId}
                    onChange={(e) => {
                      const catId = Number(e.target.value);
                      const cat = availableChildCategories.find(c => c.categoryId === catId);
                      setDetailForm({ 
                        ...detailForm, 
                        categoryId: catId,
                        itemName: cat ? cat.name : ''
                      });
                    }}
                    required
                  >
                    <option value={0}>Select category</option>
                    {availableChildCategories.map(cat => (
                      <option key={cat.categoryId} value={cat.categoryId}>
                        {cat.icon} {cat.name}
                      </option>
                    ))}
                  </select>
                </div>
              )}

              <div className="form-group">
                <label className="form-label">Item Name</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="e.g., Office Rent"
                  value={detailForm.itemName}
                  onChange={(e) => setDetailForm({ ...detailForm, itemName: e.target.value })}
                  required
                />
              </div>

              <div className="form-group">
                <label className="form-label">Budget Amount (PKR)</label>
                <input
                  type="number"
                  className="form-control"
                  value={detailForm.allocatedAmount}
                  onChange={(e) => setDetailForm({ ...detailForm, allocatedAmount: Number(e.target.value) })}
                  required
                />
              </div>

              <button type="submit" className="btn-submit">Add Item</button>
              <button type="button" className="btn-cancel" onClick={() => setShowDetailModal(false)}>
                Cancel
              </button>
            </form>
          </div>
        </div>
      )}

      <div className="bottom-nav">
        <div className="nav-items">
          <div className="nav-item" onClick={() => navigate('/dashboard')}>
            <div className="nav-icon"></div>
            <div className="nav-label">Home</div>
          </div>
          <div className="nav-item" onClick={() => navigate('/transactions')}>
            <div className="nav-icon"></div>
            <div className="nav-label">Transactions</div>
          </div>
          <div className="nav-item" onClick={() => navigate('/accounts')}>
            <div className="nav-icon"></div>
            <div className="nav-label">Accounts</div>
          </div>
          <div className="nav-item active">
            <div className="nav-icon"></div>
            <div className="nav-label">Master Budget</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MasterBudgetsPage;