// src/services/budget.service.ts



import axios from 'axios';

import { Budget, CreateBudgetDto } from '../types/budget.types';
import { API_URL_config } from '../config';
//const API_URL = 'https://localhost:7000/api/Budgets';
const API_URL = `${API_URL_config}/Budgets`;
const getAuthHeader = () => {
  const token = localStorage.getItem('token');
  return {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };
};

export const getAllBudgets = async (): Promise<Budget[]> => {
  const response = await axios.get(API_URL, getAuthHeader());
  return response.data;
};

export const getActiveBudgets = async (): Promise<Budget[]> => {
  const response = await axios.get(`${API_URL}/active`, getAuthHeader());
  return response.data;
};

export const getBudgetById = async (budgetId: number): Promise<Budget> => {
  const response = await axios.get(`${API_URL}/${budgetId}`, getAuthHeader());
  return response.data;
};

export const createBudget = async (data: CreateBudgetDto): Promise<Budget> => {
  const response = await axios.post(API_URL, data, getAuthHeader());
  return response.data;
};

export const updateBudget = async (budgetId: number, data: CreateBudgetDto): Promise<Budget> => {
  const response = await axios.put(API_URL, { budgetId, ...data }, getAuthHeader());
  return response.data;
};

export const deleteBudget = async (budgetId: number): Promise<void> => {
  await axios.delete(`${API_URL}/${budgetId}`, getAuthHeader());
};