// src/services/transaction.service.ts



import axios from 'axios';
import { Transaction, CreateTransactionDto } from '../types/transaction.types';
import { API_URL_config } from '../config';
//const API_URL = 'https://localhost:7000/api/Transactions';
const API_URL = `${API_URL_config}/Transactions`;
const getAuthHeader = () => {
  const token = localStorage.getItem('token');
  return {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };
};

export const getAllTransactions = async (): Promise<Transaction[]> => {
  const response = await axios.get(API_URL, getAuthHeader());
  return response.data;
};

export const createTransaction = async (data: CreateTransactionDto): Promise<Transaction> => {
  const response = await axios.post(API_URL, data, getAuthHeader());
  return response.data;
};

export const deleteTransaction = async (transactionId: number): Promise<void> => {
  await axios.delete(`${API_URL}/${transactionId}`, getAuthHeader());
};

export const getTotalIncome = async (startDate?: string, endDate?: string): Promise<number> => {
  const params = new URLSearchParams();
  if (startDate) params.append('startDate', startDate);
  if (endDate) params.append('endDate', endDate);
  
  const response = await axios.get(`${API_URL}/summary/income?${params}`, getAuthHeader());
  return response.data;
};

export const getTotalExpense = async (startDate?: string, endDate?: string): Promise<number> => {
  const params = new URLSearchParams();
  if (startDate) params.append('startDate', startDate);
  if (endDate) params.append('endDate', endDate);
  
  const response = await axios.get(`${API_URL}/summary/expense?${params}`, getAuthHeader());
  return response.data;
};