// src/pages/TransactionsPage.tsx

import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { 
  getAllTransactions, 
  createTransaction, 
  deleteTransaction 
} from '../services/transaction.service';
import { getAllAccounts } from '../services/account.service';
import { getAllCategories } from '../services/category.service';
import { Transaction } from '../types/transaction.types';
import { Account } from '../types/account.types';
import { Category } from '../types/category.types';

const TransactionsPage: React.FC = () => {
  const navigate = useNavigate();
  const [transactions, setTransactions] = useState<Transaction[]>([]);
  const [accounts, setAccounts] = useState<Account[]>([]);
  const [categories, setCategories] = useState<Category[]>([]);
  const [parentCategories, setParentCategories] = useState<Category[]>([]);
  const [childCategories, setChildCategories] = useState<Category[]>([]);
  const [loading, setLoading] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [filterType, setFilterType] = useState<string>('all');

  const [formData, setFormData] = useState({
    fromAccountId: 0,
    toAccountId: 0,
    parentCategoryId: 0,
    categoryId: 0,
    transactionType: 1,
    amount: 0,
    description: '',
    transactionDate: new Date().toISOString().split('T')[0]
  });

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
      navigate('/login');
      return;
    }
    fetchData();
  }, [navigate]);

  useEffect(() => {
    // Separate parents and childrena
    const parents = categories.filter(c => !c.parentCategoryId && c.categoryType === 'Expense');
    const children = categories.filter(c => c.parentCategoryId);
    setParentCategories(parents);
    setChildCategories(children);
  }, [categories]);

  const fetchData = async () => {
    try {
      setLoading(true);
      const [txnsData, accountsData, categoriesData] = await Promise.all([
        getAllTransactions(),
        getAllAccounts(),
        getAllCategories()
      ]);
      
      console.log(' Accounts Data:', accountsData);
      console.log(' First Account:', accountsData[0]);
      
      setTransactions(txnsData);
      setAccounts(accountsData);
      setCategories(categoriesData);
    } catch (error) {
      console.error('Failed to fetch data:', error);
    } finally {
      setLoading(false);
    }
  };

 const handleSubmit = async (e: React.FormEvent) => {
  e.preventDefault();
  
  console.log(' Form Data:', formData);
  console.log(' Transaction Type:', formData.transactionType);
  console.log(' From Account ID:', formData.fromAccountId);
  console.log(' Category ID:', formData.categoryId);
  
  // Validation
  if (formData.fromAccountId === 0) {
    alert('Please select an account');
    return;
  }
  
  if (formData.categoryId === 0) {
    alert('Please select a category');
    return;
  }
  
  try {
    const dto: any = {
      fromAccountId: formData.fromAccountId,
      categoryId: formData.categoryId,
      transactionType: formData.transactionType,
      amount: formData.amount,
      description: formData.description,
      transactionDate: formData.transactionDate
    };

    // Add toAccountId ONLY for Transfer (type 3)
    if (formData.transactionType === 2) {
      if (formData.toAccountId === 0) {
        alert('Please select To Account for transfer');
        return;
      }
      dto.toAccountId = formData.toAccountId;
    }

    console.log(' Sending DTO:', dto);
    
    await createTransaction(dto);
    
    alert('Transaction created successfully!');
    
    setShowModal(false);
    setFormData({
      fromAccountId: 0,
      toAccountId: 0,
      parentCategoryId: 0,
      categoryId: 0,
      transactionType: 1,
      amount: 0,
      description: '',
      transactionDate: new Date().toISOString().split('T')[0]
    });
    fetchData();
  } catch (error: any) {
    console.error(' Transaction Error:', error);
    console.error(' Error Response:', error.response?.data);
    alert(`Failed: ${error.response?.data?.message || error.message}`);
  }
};

  const handleDelete = async (transactionId: number) => {
    if (!window.confirm('Delete this transaction?')) return;
    try {
      await deleteTransaction(transactionId);
      fetchData();
    } catch (error) {
      alert('Failed to delete transaction');
    }
  };

  const getTypeIcon = (txn: Transaction) => {
    const categoryName = txn.categoryName?.toLowerCase() || '';
    
    // Category-based icons
    if (categoryName.includes('food') || categoryName.includes('dining')) return '';
    if (categoryName.includes('groceries')) return '';
    if (categoryName.includes('restaurant')) return '';
    if (categoryName.includes('fast food')) return '';
    if (categoryName.includes('cafe') || categoryName.includes('coffee')) return '';
    if (categoryName.includes('transport')) return '';
    if (categoryName.includes('fuel')) return '';
    if (categoryName.includes('public transport')) return '';
    if (categoryName.includes('taxi') || categoryName.includes('uber') || categoryName.includes('ride')) return '';
    if (categoryName.includes('maintenance')) return '';
    if (categoryName.includes('shopping')) return '';
    if (categoryName.includes('clothing')) return '';
    if (categoryName.includes('electronics')) return '';
    if (categoryName.includes('books')) return '';
    if (categoryName.includes('gifts')) return '';
    if (categoryName.includes('bills') || categoryName.includes('utilities')) return '';
    if (categoryName.includes('electricity')) return '';
    if (categoryName.includes('water')) return '';
    if (categoryName.includes('gas')) return '';
    if (categoryName.includes('internet')) return '';
    if (categoryName.includes('phone')) return '';
    if (categoryName.includes('entertainment')) return '';
    if (categoryName.includes('movies')) return '';
    if (categoryName.includes('games')) return '';
    if (categoryName.includes('music') || categoryName.includes('streaming')) return '';
    if (categoryName.includes('sports')) return '';
    if (categoryName.includes('health') || categoryName.includes('fitness') || categoryName.includes('medical')) return '';
    if (categoryName.includes('doctor')) return '';
    if (categoryName.includes('pharmacy')) return '';
    if (categoryName.includes('gym')) return '';
    if (categoryName.includes('dental')) return '';
    if (categoryName.includes('education') || categoryName.includes('school')) return '';
    if (categoryName.includes('fees')) return '';
    if (categoryName.includes('courses')) return '';
    if (categoryName.includes('stationery')) return '';
    if (categoryName.includes('home') || categoryName.includes('living')) return '';
    if (categoryName.includes('rent')) return '';
    if (categoryName.includes('furniture')) return '';
    if (categoryName.includes('appliances')) return '';
    if (categoryName.includes('personal care')) return '';
    if (categoryName.includes('salon')) return '';
    if (categoryName.includes('cosmetics')) return '';
    if (categoryName.includes('insurance')) return '';
    if (categoryName.includes('travel')) return '';
    if (categoryName.includes('hotel')) return '';
    if (categoryName.includes('vacation')) return '';
    if (categoryName.includes('salary')) return '';
    if (categoryName.includes('freelance')) return '';
    if (categoryName.includes('business')) return '';
    if (categoryName.includes('investment')) return '';
    if (categoryName.includes('bonus')) return '';
    if (categoryName.includes('rental')) return '';
    if (categoryName.includes('charity')) return '';
    if (categoryName.includes('pets')) return '';
    if (categoryName.includes('savings') || categoryName.includes('bank')) return '';
    if (categoryName.includes('emergency')) return '';
    if (categoryName.includes('atm')) return '';
    
    // Fallback based on transaction type
    if (txn.transactionType === 0) return '';
    if (txn.transactionType === 1) return '';
    if (txn.transactionType === 2) return '';
    
    return '';
  };

  const getTypeColor = (type: number) => {
    if (type === 0) return '#10b981';
    if (type === 1) return '#ef4444';
    return '#3b82f6';
  };

  const getTypeName = (type: number) => {
    if (type === 0) return 'Income';
    if (type === 1) return 'Expense';
    return 'Transfer';
  };

  const filteredTransactions = transactions.filter(txn => {
    if (filterType === 'all') return true;
    if (filterType === 'income') return txn.transactionType === 0;
    if (filterType === 'expense') return txn.transactionType === 1;
    if (filterType === 'transfer') return txn.transactionType === 2;
    return true;
  });

  // Get available child categories based on selected parent
  const availableChildCategories = formData.parentCategoryId 
    ? childCategories.filter(c => c.parentCategoryId === formData.parentCategoryId)
    : [];

  if (loading) {
    return (
      <div className="d-flex justify-content-center align-items-center min-vh-100">
        <div className="spinner-border text-primary"></div>
      </div>
    );
  }

  return (
    <div className="transactions-container">
      <style>{`
        .transactions-container {
          min-height: 100vh;
          background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
          padding-bottom: 80px;
        }

        .top-header {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
          padding: 1rem;
          border-radius: 0 0 20px 20px;
          box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
        }

        .header-content {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }

        .page-title {
          font-size: 1.5rem;
          font-weight: 700;
          margin: 0;
        }

        .add-btn {
          background: rgba(255, 255, 255, 0.2);
          border: 1px solid rgba(255, 255, 255, 0.3);
          color: white;
          padding: 0.5rem 1rem;
          border-radius: 15px;
          font-size: 0.9rem;
        }

        .filter-tabs {
          display: flex;
          gap: 0.5rem;
          padding: 1rem;
          overflow-x: auto;
        }

        .filter-tab {
          background: white;
          border: none;
          padding: 0.5rem 1rem;
          border-radius: 20px;
          font-size: 0.85rem;
          font-weight: 600;
          white-space: nowrap;
          transition: all 0.3s ease;
        }

        .filter-tab.active {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
        }

        .transactions-list {
          padding: 0 1rem 1rem;
        }

        .transaction-card {
          background: white;
          border-radius: 15px;
          padding: 1rem;
          margin-bottom: 0.75rem;
          box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
          display: flex;
          align-items: center;
          gap: 1rem;
        }

        .txn-icon {
          width: 50px;
          height: 50px;
          border-radius: 12px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 1.5rem;
          flex-shrink: 0;
        }

        .txn-details {
          flex: 1;
          min-width: 0;
        }

        .txn-category {
          font-weight: 600;
          color: #2d3748;
          margin: 0;
          font-size: 0.95rem;
        }

        .txn-parent {
          font-size: 0.75rem;
          color: #6c757d;
          margin-top: 0.1rem;
        }

        .txn-description {
          font-size: 0.8rem;
          color: #6c757d;
          margin: 0.2rem 0 0;
        }

        .txn-date {
          font-size: 0.75rem;
          color: #9ca3af;
        }

        .txn-amount-section {
          text-align: right;
        }

        .txn-amount {
          font-weight: 700;
          font-size: 1.1rem;
          margin: 0;
        }

        .txn-type {
          font-size: 0.75rem;
          margin-top: 0.2rem;
        }

        .btn-delete {
          background: #dc3545;
          color: white;
          border: none;
          padding: 0.3rem 0.6rem;
          border-radius: 8px;
          font-size: 0.75rem;
          margin-top: 0.3rem;
        }

        .empty-state {
          text-align: center;
          padding: 3rem 1rem;
        }

        .empty-icon {
          font-size: 4rem;
          margin-bottom: 1rem;
        }

        .modal-overlay {
          position: fixed;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(0, 0, 0, 0.5);
          display: flex;
          align-items: center;
          justify-content: center;
          z-index: 1000;
          padding: 1rem;
        }

        .modal-content {
          background: white;
          border-radius: 20px;
          padding: 1.5rem;
          max-width: 500px;
          width: 100%;
          max-height: 90vh;
          overflow-y: auto;
        }

        .modal-title {
          font-size: 1.3rem;
          font-weight: 700;
          margin-bottom: 1rem;
        }

        .form-group {
          margin-bottom: 1rem;
        }

        .form-label {
          font-weight: 600;
          margin-bottom: 0.5rem;
          display: block;
          font-size: 0.9rem;
        }

        .form-control, .form-select {
          border-radius: 12px;
          padding: 0.75rem;
          border: 2px solid #e9ecef;
          width: 100%;
        }

        .type-selector {
          display: flex;
          gap: 0.5rem;
          margin-bottom: 1rem;
        }

        .type-btn {
          flex: 1;
          padding: 0.75rem;
          border: 2px solid #e9ecef;
          background: white;
          border-radius: 12px;
          font-weight: 600;
          transition: all 0.3s ease;
        }

        .type-btn.active {
          border-color: #667eea;
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
        }

        .btn-submit {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          border: none;
          color: white;
          padding: 0.75rem;
          border-radius: 12px;
          width: 100%;
          font-weight: 600;
          margin-top: 0.5rem;
        }

        .btn-cancel {
          background: #6c757d;
          border: none;
          color: white;
          padding: 0.75rem;
          border-radius: 12px;
          width: 100%;
          font-weight: 600;
          margin-top: 0.5rem;
        }

        .bottom-nav {
          position: fixed;
          bottom: 0;
          left: 0;
          right: 0;
          background: white;
          border-top: 1px solid #e9ecef;
          padding: 0.5rem 0;
          box-shadow: 0 -4px 15px rgba(0, 0, 0, 0.05);
          z-index: 1000;
        }

        .nav-items {
          display: flex;
          justify-content: space-around;
          max-width: 600px;
          margin: 0 auto;
        }

        .nav-item {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 0.2rem;
          padding: 0.4rem 0.75rem;
          border-radius: 10px;
          cursor: pointer;
          color: #6c757d;
          flex: 1;
          max-width: 80px;
        }

        .nav-item.active {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
        }

        .nav-icon {
          font-size: 1.3rem;
        }

        .nav-label {
          font-size: 0.7rem;
          font-weight: 600;
        }
      `}</style>

      <div className="top-header">
        <div className="header-content">
          <h1 className="page-title"> Transactions</h1>
          <button className="add-btn btn" onClick={() => setShowModal(true)}>
             Add
          </button>
        </div>
      </div>

      <div className="filter-tabs">
        <button 
          className={`filter-tab ${filterType === 'all' ? 'active' : ''}`}
          onClick={() => setFilterType('all')}
        >
          All ({transactions.length})
        </button>
        <button 
          className={`filter-tab ${filterType === 'income' ? 'active' : ''}`}
          onClick={() => setFilterType('income')}
        >
           Income ({transactions.filter(t => t.transactionType === 0).length})
        </button>
        <button 
          className={`filter-tab ${filterType === 'expense' ? 'active' : ''}`}
          onClick={() => setFilterType('expense')}
        >
           Expense ({transactions.filter(t => t.transactionType === 1).length})
        </button>
        <button 
          className={`filter-tab ${filterType === 'transfer' ? 'active' : ''}`}
          onClick={() => setFilterType('transfer')}
        >
           Transfer ({transactions.filter(t => t.transactionType === 2).length})
        </button>
      </div>

      <div className="transactions-list">
        {filteredTransactions.length === 0 ? (
          <div className="empty-state">
            <div className="empty-icon"></div>
            <p>No transactions yet. Add your first one!</p>
          </div>
        ) : (
          filteredTransactions.map((txn) => (
            <div key={txn.transactionId} className="transaction-card">
              <div 
                className="txn-icon" 
                style={{ background: `${getTypeColor(txn.transactionType)}20` }}
              >
                {getTypeIcon(txn)}
              </div>
              <div className="txn-details">
                <h3 className="txn-category">{txn.categoryName}</h3>
                {txn.parentCategoryName && (
                  <div className="txn-parent"> {txn.parentCategoryName}</div>
                )}
                <p className="txn-description">{txn.description}</p>
                <div className="txn-date">
                  {new Date(txn.transactionDate).toLocaleDateString()}  {txn.fromAccountName}
                </div>
              </div>
              <div className="txn-amount-section">
                <h3 
  className="txn-amount" 
  style={{ color: getTypeColor(txn.transactionType) }}
>
  {txn.transactionType === 1 ? '-' : '+'} PKR {txn.amount.toLocaleString()}
</h3>
                <div className="txn-type" style={{ color: getTypeColor(txn.transactionType) }}>
                  {getTypeName(txn.transactionType)}
                </div>
                <button 
                  className="btn-delete"
                  onClick={() => handleDelete(txn.transactionId)}
                >
                  
                </button>
              </div>
            </div>
          ))
        )}
      </div>

      {showModal && (
        <div className="modal-overlay" onClick={() => setShowModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <h2 className="modal-title">Add Transaction</h2>
            <form onSubmit={handleSubmit}>
          <div className="type-selector">
  <button
    type="button"
    className={`type-btn ${formData.transactionType === 0 ? 'active' : ''}`}
    onClick={() => {
      console.log(' Income button clicked');
      setFormData({ 
        ...formData, 
        transactionType: 0, 
        parentCategoryId: 0, 
        categoryId: 0,
        toAccountId: 0
      });
    }}
  >
     Income
  </button>
  <button
    type="button"
    className={`type-btn ${formData.transactionType === 1 ? 'active' : ''}`}
    onClick={() => {
      console.log(' Expense button clicked');
      setFormData({ 
        ...formData, 
        transactionType: 1, 
        parentCategoryId: 0, 
        categoryId: 0,
        toAccountId: 0
      });
    }}
  >
     Expense
  </button>
  <button
    type="button"
    className={`type-btn ${formData.transactionType === 2 ? 'active' : ''}`}
    onClick={() => {
      console.log(' Transfer button clicked');
      setFormData({ 
        ...formData, 
        transactionType: 2, 
        parentCategoryId: 0, 
        categoryId: 0 
      });
    }}
  >
     Transfer
  </button>
</div>
              <div className="form-group">
  <label className="form-label">{formData.transactionType === 2 ? 'From Account' : 'Account'}</label>
  <select
    className="form-select"
    value={formData.fromAccountId}
    onChange={(e) => {
      const accountId = Number(e.target.value);
      console.log(' Account Selected:', accountId);
      setFormData({ ...formData, fromAccountId: accountId });
    }}
    required
  >
    <option value={0}>Select account</option>
    {accounts.map(acc => (
      <option key={acc.accountId} value={acc.accountId}>
        {acc.name} (PKR {acc.balance.toLocaleString()})
      </option>
    ))}
  </select>
</div>

              {formData.transactionType === 2 && (
                <div className="form-group">
                  <label className="form-label">To Account</label>
                  <select
                    className="form-select"
                    value={formData.toAccountId}
                    onChange={(e) => setFormData({ ...formData, toAccountId: Number(e.target.value) })}
                    required
                  >
                    <option value={0}>Select account</option>
                    {accounts.map(acc => (
                      <option key={acc.accountId} value={acc.accountId}>
                        {acc.name} (PKR {acc.balance.toLocaleString()})
                      </option>
                    ))}
                  </select>
                </div>
              )}

              {formData.transactionType === 0 && (
                <div className="form-group">
                  <label className="form-label">Income Category</label>
                  <select
                    className="form-select"
                    value={formData.categoryId}
                    onChange={(e) => setFormData({ ...formData, categoryId: Number(e.target.value) })}
                    required
                  >
                    <option value={0}>Select income category</option>
                    {categories
                      .filter(c => c.categoryType === 'Income')
                      .map(cat => (
                        <option key={cat.categoryId} value={cat.categoryId}>
                          {cat.icon} {cat.name}
                        </option>
                      ))}
                  </select>
                </div>
              )}

              {formData.transactionType === 1 && (
                <>
                  <div className="form-group">
                    <label className="form-label">Parent Category</label>
                    <select
                      className="form-select"
                      value={formData.parentCategoryId}
                      onChange={(e) => setFormData({ 
                        ...formData, 
                        parentCategoryId: Number(e.target.value),
                        categoryId: 0
                      })}
                      required
                    >
                      <option value={0}>Select parent category</option>
                      {parentCategories.map(parent => (
                        <option key={parent.categoryId} value={parent.categoryId}>
                          {parent.icon} {parent.name}
                        </option>
                      ))}
                    </select>
                  </div>

                  {formData.parentCategoryId > 0 && (
                    <div className="form-group">
                      <label className="form-label">Expense Category</label>
                      <select
                        className="form-select"
                        value={formData.categoryId}
                        onChange={(e) => setFormData({ ...formData, categoryId: Number(e.target.value) })}
                        required
                      >
                        <option value={0}>Select expense category</option>
                        {availableChildCategories.map(cat => (
                          <option key={cat.categoryId} value={cat.categoryId}>
                            {cat.icon} {cat.name}
                          </option>
                        ))}
                      </select>
                    </div>
                  )}
                </>
              )}

              <div className="form-group">
                <label className="form-label">Amount (PKR)</label>
                <input
                  type="number"
                  className="form-control"
                  value={formData.amount}
                  onChange={(e) => setFormData({ ...formData, amount: Number(e.target.value) })}
                  required
                />
              </div>

              <div className="form-group">
                <label className="form-label">Description</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="e.g., Lunch at restaurant"
                  value={formData.description}
                  onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                  required
                />
              </div>

              <div className="form-group">
                <label className="form-label">Date</label>
                <input
                  type="date"
                  className="form-control"
                  value={formData.transactionDate}
                  onChange={(e) => setFormData({ ...formData, transactionDate: e.target.value })}
                  required
                />
              </div>

              <button type="submit" className="btn-submit">Add Transaction</button>
              <button type="button" className="btn-cancel" onClick={() => setShowModal(false)}>
                Cancel
              </button>
            </form>
          </div>
        </div>
      )}

      <div className="bottom-nav">
        <div className="nav-items">
          <div className="nav-item" onClick={() => navigate('/dashboard')}>
            <div className="nav-icon"></div>
            <div className="nav-label">Home</div>
          </div>
          <div className="nav-item active">
            <div className="nav-icon"></div>
            <div className="nav-label">Transactions</div>
          </div>
          <div className="nav-item" onClick={() => navigate('/accounts')}>
            <div className="nav-icon"></div>
            <div className="nav-label">Accounts</div>
          </div>
          <div className="nav-item" onClick={() => navigate('/categories')}>
            <div className="nav-icon"></div>
            <div className="nav-label">Categories</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TransactionsPage;