// src/services/auth.service.ts

import axios from 'axios';
import { LoginDto, RegisterDto, AuthResponse } from '../types/auth.types';
import { API_URL_config } from '../config';

//  Apna backend URL yahan dalo
const API_URL = `${API_URL_config}/Auth`;

// Login API call
export const loginUser = async (data: LoginDto): Promise<AuthResponse> => {
  const response = await axios.post(`${API_URL}/login`, data);

   if (response.data.token) {
    localStorage.setItem('token', response.data.token);
  }
  return response.data;
};

// Register API call
export const registerUser = async (data: RegisterDto): Promise<AuthResponse> => {
  const response = await axios.post(`${API_URL}/register`, data);
  return response.data;
};