// src/pages/CategoriesPage.tsx

import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { getAllCategories, createCategory, deleteCategory, seedCategories } from '../services/category.service';
import { Category, CreateCategoryDto } from '../types/category.types';

const CategoriesPage: React.FC = () => {
  const navigate = useNavigate();
  const [categories, setCategories] = useState<Category[]>([]);
  const [parentCategories, setParentCategories] = useState<Category[]>([]);
  const [childCategories, setChildCategories] = useState<Category[]>([]);
  const [loading, setLoading] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [filterType, setFilterType] = useState<string>('all');
  const [selectedParent, setSelectedParent] = useState<number | null>(null);

  const [formData, setFormData] = useState<CreateCategoryDto>({
    name: '',
    categoryType: 'Expense',
    icon: '',
    colorCode: '#667eea',
    parentCategoryId: undefined
  });

  const iconOptions = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];
  const colorOptions = ['#667eea', '#10b981', '#ef4444', '#f59e0b', '#3b82f6', '#8b5cf6', '#ec4899', '#14b8a6'];

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
      navigate('/login');
      return;
    }
    fetchCategories();
  }, [navigate]);

  useEffect(() => {
    // Separate parents and children
    const parents = categories.filter(c => !c.parentCategoryId);
    const children = categories.filter(c => c.parentCategoryId);
    setParentCategories(parents);
    setChildCategories(children);
  }, [categories]);

  const fetchCategories = async () => {
    try {
      setLoading(true);
      const data = await getAllCategories();
      setCategories(data);
    } catch (error) {
      console.error('Failed to fetch categories:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      await createCategory(formData);
      setShowModal(false);
      setFormData({
        name: '',
        categoryType: 'Expense',
        icon: '',
        colorCode: '#667eea',
        parentCategoryId: undefined
      });
      fetchCategories();
    } catch (error) {
      alert('Failed to create category');
    }
  };

  const handleDelete = async (categoryId: number) => {
    if (!window.confirm('Delete this category?')) return;
    try {
      await deleteCategory(categoryId);
      fetchCategories();
    } catch (error) {
      alert('Failed to delete category');
    }
  };

  const handleSeed = async () => {
    if (!window.confirm('This will add default categories. Continue?')) return;
    try {
      await seedCategories();
      fetchCategories();
    } catch (error) {
      alert('Failed to seed categories');
    }
  };

  // Filter categories
  const getFilteredCategories = () => {
    let filtered = categories;

    // Filter by type
    if (filterType === 'income') {
      filtered = filtered.filter(c => c.categoryType === 'Income');
    } else if (filterType === 'expense') {
      filtered = filtered.filter(c => c.categoryType === 'Expense');
    }

    // Filter by parent
    if (selectedParent !== null) {
      filtered = filtered.filter(c => c.parentCategoryId === selectedParent);
    } else {
      // Show only parents if no parent selected
      filtered = filtered.filter(c => !c.parentCategoryId);
    }

    return filtered;
  };

  const filteredCategories = getFilteredCategories();

  // Get parent name
  const getParentName = (parentId?: number) => {
    if (!parentId) return null;
    const parent = parentCategories.find(p => p.categoryId === parentId);
    return parent ? parent.name : null;
  };

  // Get children count
  const getChildrenCount = (parentId: number) => {
    return childCategories.filter(c => c.parentCategoryId === parentId).length;
  };

  if (loading) {
    return (
      <div className="d-flex justify-content-center align-items-center min-vh-100">
        <div className="spinner-border text-primary"></div>
      </div>
    );
  }

  return (
    <div className="categories-container">
      <style>{`
        .categories-container {
          min-height: 100vh;
          background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
          padding-bottom: 80px;
        }

        .top-header {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
          padding: 1rem;
          border-radius: 0 0 20px 20px;
          box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
        }

        .header-content {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 0.75rem;
        }

        .page-title {
          font-size: 1.5rem;
          font-weight: 700;
          margin: 0;
        }

        .header-actions {
          display: flex;
          gap: 0.5rem;
        }

        .add-btn, .seed-btn {
          background: rgba(255, 255, 255, 0.2);
          border: 1px solid rgba(255, 255, 255, 0.3);
          color: white;
          padding: 0.5rem 1rem;
          border-radius: 15px;
          font-size: 0.9rem;
        }

        .breadcrumb {
          display: flex;
          align-items: center;
          gap: 0.5rem;
          font-size: 0.85rem;
          opacity: 0.9;
        }

        .breadcrumb-item {
          cursor: pointer;
          padding: 0.25rem 0.5rem;
          border-radius: 8px;
          transition: background 0.2s;
        }

        .breadcrumb-item:hover {
          background: rgba(255, 255, 255, 0.1);
        }

        .breadcrumb-item.active {
          background: rgba(255, 255, 255, 0.2);
          font-weight: 600;
        }

        .filter-tabs {
          display: flex;
          gap: 0.5rem;
          padding: 1rem;
          overflow-x: auto;
        }

        .filter-tab {
          background: white;
          border: none;
          padding: 0.5rem 1rem;
          border-radius: 20px;
          font-size: 0.85rem;
          font-weight: 600;
          white-space: nowrap;
          transition: all 0.3s ease;
        }

        .filter-tab.active {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
        }

        .categories-grid {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          gap: 0.75rem;
          padding: 0 1rem 1rem;
        }

        @media (min-width: 576px) {
          .categories-grid {
            grid-template-columns: repeat(3, 1fr);
          }
        }

        @media (min-width: 768px) {
          .categories-grid {
            grid-template-columns: repeat(4, 1fr);
          }
        }

        .category-card {
          background: white;
          border-radius: 15px;
          padding: 1rem;
          box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
          position: relative;
          transition: transform 0.2s ease;
          cursor: pointer;
        }

        .category-card:active {
          transform: scale(0.98);
        }

        .category-card.parent {
          border: 2px solid #667eea;
        }

        .delete-btn {
          position: absolute;
          top: 0.5rem;
          right: 0.5rem;
          background: #dc3545;
          color: white;
          border: none;
          width: 24px;
          height: 24px;
          border-radius: 50%;
          font-size: 0.9rem;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
        }

        .category-icon-display {
          width: 50px;
          height: 50px;
          border-radius: 12px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 1.8rem;
          margin-bottom: 0.5rem;
        }

        .category-name {
          font-weight: 600;
          color: #2d3748;
          margin: 0;
          font-size: 0.9rem;
        }

        .category-type {
          font-size: 0.75rem;
          color: #6c757d;
          margin-top: 0.25rem;
        }

        .category-parent {
          font-size: 0.7rem;
          color: #667eea;
          margin-top: 0.25rem;
          font-weight: 600;
        }

        .children-count {
          font-size: 0.7rem;
          background: #667eea;
          color: white;
          padding: 0.2rem 0.5rem;
          border-radius: 10px;
          margin-top: 0.5rem;
          display: inline-block;
        }

        .empty-state {
          text-align: center;
          padding: 3rem 1rem;
        }

        .empty-icon {
          font-size: 4rem;
          margin-bottom: 1rem;
        }

        .modal-overlay {
          position: fixed;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: rgba(0, 0, 0, 0.5);
          display: flex;
          align-items: center;
          justify-content: center;
          z-index: 1000;
          padding: 1rem;
        }

        .modal-content {
          background: white;
          border-radius: 20px;
          padding: 1.5rem;
          max-width: 500px;
          width: 100%;
          max-height: 90vh;
          overflow-y: auto;
        }

        .modal-title {
          font-size: 1.3rem;
          font-weight: 700;
          margin-bottom: 1rem;
        }

        .form-group {
          margin-bottom: 1rem;
        }

        .form-label {
          font-weight: 600;
          margin-bottom: 0.5rem;
          display: block;
          font-size: 0.9rem;
        }

        .form-control, .form-select {
          border-radius: 12px;
          padding: 0.75rem;
          border: 2px solid #e9ecef;
          width: 100%;
        }

        .type-selector {
          display: flex;
          gap: 0.5rem;
          margin-bottom: 1rem;
        }

        .type-btn {
          flex: 1;
          padding: 0.75rem;
          border: 2px solid #e9ecef;
          background: white;
          border-radius: 12px;
          font-weight: 600;
          transition: all 0.3s ease;
        }

        .type-btn.active {
          border-color: #667eea;
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
        }

        .icon-grid, .color-grid {
          display: grid;
          grid-template-columns: repeat(6, 1fr);
          gap: 0.5rem;
          margin-top: 0.5rem;
        }

        .icon-option, .color-option {
          width: 100%;
          aspect-ratio: 1;
          border: 2px solid #e9ecef;
          border-radius: 10px;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          transition: all 0.2s ease;
          font-size: 1.5rem;
        }

        .icon-option.selected, .color-option.selected {
          border-color: #667eea;
          background: #f0f4ff;
          transform: scale(1.1);
        }

        .color-option {
          border-radius: 50%;
        }

        .btn-submit {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          border: none;
          color: white;
          padding: 0.75rem;
          border-radius: 12px;
          width: 100%;
          font-weight: 600;
          margin-top: 0.5rem;
        }

        .btn-cancel {
          background: #6c757d;
          border: none;
          color: white;
          padding: 0.75rem;
          border-radius: 12px;
          width: 100%;
          font-weight: 600;
          margin-top: 0.5rem;
        }

        .bottom-nav {
          position: fixed;
          bottom: 0;
          left: 0;
          right: 0;
          background: white;
          border-top: 1px solid #e9ecef;
          padding: 0.5rem 0;
          box-shadow: 0 -4px 15px rgba(0, 0, 0, 0.05);
          z-index: 1000;
        }

        .nav-items {
          display: flex;
          justify-content: space-around;
          max-width: 600px;
          margin: 0 auto;
        }

        .nav-item {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 0.2rem;
          padding: 0.4rem 0.75rem;
          border-radius: 10px;
          cursor: pointer;
          color: #6c757d;
          flex: 1;
          max-width: 80px;
        }

        .nav-item.active {
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
          color: white;
        }

        .nav-icon {
          font-size: 1.3rem;
        }

        .nav-label {
          font-size: 0.7rem;
          font-weight: 600;
        }
      `}</style>

      <div className="top-header">
        <div className="header-content">
          <h1 className="page-title"> Categories</h1>
          <div className="header-actions">
            <button className="seed-btn btn" onClick={handleSeed}>
               Seed
            </button>
            <button className="add-btn btn" onClick={() => setShowModal(true)}>
               Add
            </button>
          </div>
        </div>

        {/* Breadcrumb */}
        <div className="breadcrumb">
          <div 
            className={`breadcrumb-item ${selectedParent === null ? 'active' : ''}`}
            onClick={() => setSelectedParent(null)}
          >
             All Parents
          </div>
          {selectedParent !== null && (
            <>
              <span></span>
              <div className="breadcrumb-item active">
                {parentCategories.find(p => p.categoryId === selectedParent)?.icon} {parentCategories.find(p => p.categoryId === selectedParent)?.name}
              </div>
            </>
          )}
        </div>
      </div>

      <div className="filter-tabs">
        <button 
          className={`filter-tab ${filterType === 'all' ? 'active' : ''}`}
          onClick={() => setFilterType('all')}
        >
          All ({categories.length})
        </button>
        <button 
          className={`filter-tab ${filterType === 'income' ? 'active' : ''}`}
          onClick={() => setFilterType('income')}
        >
           Income ({categories.filter(c => c.categoryType === 'Income').length})
        </button>
        <button 
          className={`filter-tab ${filterType === 'expense' ? 'active' : ''}`}
          onClick={() => setFilterType('expense')}
        >
           Expense ({categories.filter(c => c.categoryType === 'Expense').length})
        </button>
      </div>

      <div className="categories-grid">
        {filteredCategories.length === 0 ? (
          <div className="empty-state" style={{ gridColumn: '1 / -1' }}>
            <div className="empty-icon"></div>
            <p>No categories found.</p>
          </div>
        ) : (
          filteredCategories.map((cat) => {
            const isParent = !cat.parentCategoryId;
            const childCount = isParent ? getChildrenCount(cat.categoryId) : 0;
            const parentName = getParentName(cat.parentCategoryId);

            return (
              <div 
                key={cat.categoryId} 
                className={`category-card ${isParent ? 'parent' : ''}`}
                onClick={() => {
                  if (isParent && selectedParent === null) {
                    setSelectedParent(cat.categoryId);
                  }
                }}
              >
                <button 
                  className="delete-btn"
                  onClick={(e) => {
                    e.stopPropagation();
                    handleDelete(cat.categoryId);
                  }}
                >
                  
                </button>
                <div 
                  className="category-icon-display"
                  style={{ background: `${cat.colorCode}20` }}
                >
                  {cat.icon}
                </div>
                <h3 className="category-name">{cat.name}</h3>
                <p className="category-type">{cat.categoryType}</p>
                {parentName && (
                  <p className="category-parent"> {parentName}</p>
                )}
                {isParent && childCount > 0 && (
                  <span className="children-count">
                    {childCount} sub-categories
                  </span>
                )}
              </div>
            );
          })
        )}
      </div>

      {showModal && (
        <div className="modal-overlay" onClick={() => setShowModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <h2 className="modal-title">Add Category</h2>
            <form onSubmit={handleSubmit}>
              <div className="type-selector">
                <button
                  type="button"
                  className={`type-btn ${formData.categoryType === 'Income' ? 'active' : ''}`}
                  onClick={() => setFormData({ ...formData, categoryType: 'Income', parentCategoryId: undefined })}
                >
                   Income
                </button>
                <button
                  type="button"
                  className={`type-btn ${formData.categoryType === 'Expense' ? 'active' : ''}`}
                  onClick={() => setFormData({ ...formData, categoryType: 'Expense' })}
                >
                   Expense
                </button>
              </div>

              {formData.categoryType === 'Expense' && (
                <div className="form-group">
                  <label className="form-label">Parent Category (Optional)</label>
                  <select
                    className="form-select"
                    value={formData.parentCategoryId || 0}
                    onChange={(e) => setFormData({ ...formData, parentCategoryId: Number(e.target.value) || undefined })}
                  >
                    <option value={0}>None (Create as parent)</option>
                    {parentCategories
                      .filter(p => p.categoryType === 'Expense')
                      .map(parent => (
                        <option key={parent.categoryId} value={parent.categoryId}>
                          {parent.icon} {parent.name}
                        </option>
                      ))}
                  </select>
                </div>
              )}

              <div className="form-group">
                <label className="form-label">Name</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="e.g., Food & Dining"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  required
                />
              </div>

              <div className="form-group">
                <label className="form-label">Icon</label>
                <div className="icon-grid">
                  {iconOptions.map(icon => (
                    <div
                      key={icon}
                      className={`icon-option ${formData.icon === icon ? 'selected' : ''}`}
                      onClick={() => setFormData({ ...formData, icon })}
                    >
                      {icon}
                    </div>
                  ))}
                </div>
              </div>

              <div className="form-group">
                <label className="form-label">Color</label>
                <div className="color-grid">
                  {colorOptions.map(color => (
                    <div
                      key={color}
                      className={`color-option ${formData.colorCode === color ? 'selected' : ''}`}
                      style={{ background: color }}
                      onClick={() => setFormData({ ...formData, colorCode: color })}
                    />
                  ))}
                </div>
              </div>

              <button type="submit" className="btn-submit">Add Category</button>
              <button type="button" className="btn-cancel" onClick={() => setShowModal(false)}>
                Cancel
              </button>
            </form>
          </div>
        </div>
      )}

      <div className="bottom-nav">
        <div className="nav-items">
          <div className="nav-item" onClick={() => navigate('/dashboard')}>
            <div className="nav-icon"></div>
            <div className="nav-label">Home</div>
          </div>
          <div className="nav-item" onClick={() => navigate('/transactions')}>
            <div className="nav-icon"></div>
            <div className="nav-label">Transactions</div>
          </div>
          <div className="nav-item" onClick={() => navigate('/accounts')}>
            <div className="nav-icon"></div>
            <div className="nav-label">Accounts</div>
          </div>
          <div className="nav-item active">
            <div className="nav-icon"></div>
            <div className="nav-label">Categories</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CategoriesPage;